import { configureStore, createSlice } from "@reduxjs/toolkit";

// state -creatSlice({})
let user = createSlice({
    name: 'user', //state 
    //initialState: ''
    initialState: {name: '', likeCount: 20},

    //state  => Redux state 
    //1.   
    //2.   store 
    reducers: {
        changeName(state) {
           state.name = ""
        },

        increase(state, a) {
            state.likeCount += a.payload; //          payoad   
        }
    }
})

//  state  
export let {changeName, increase} = user.actions

//cart  state 
let cart = createSlice({
    name: 'cart',
    initialState: [
        {
            id: 0,
            name: "_Wings",
            count: 1
        },
        {
            id: 1,
            name: "",
            count: 10
        },
        {
            id: 2,
            name: " ",
            count: 5
        },
    ],
    reducers : {
        addCount(state, action) {
            //       findIndex
            //action      
            let num = state.findIndex((item)=> {
                return item.id === action.payload
            })
            state[num].count++
        },
        removeItem(state, action) {
            const index = state.findIndex(item => item.id === action.payload);
            if (index !== -1) {
                state.splice(index,1)
            }
        },
        addItem(state, action) {
            state.push(action.payload)
        }
    }
})

export const {addCount, removeItem, addItem } = cart.actions;


///  state 
let bookInfo = createSlice({
    name: 'bookInfo',
    initialState: {title: '', price: 20000}
})


export default configureStore({    
    reducer: {
        //store  
        user: user.reducer,
        cart: cart.reducer,
        bookInfo: bookInfo.reducer
    }
})