import React from 'react';
import { useState, useEffect } from 'react';

const Sub3 = () => {

  let [count, setCount] = useState(0);
  let [alert, setAlert] = useState(true);

  useEffect(() => {

    let a = setTimeout(() => { setAlert(false) }, 5000);
    console.log(2);

    return () => {
      clearTimeout(a);
      //   
    }
  }, [count])
  return (
    <div className='event_wrap'>
      <div className='event_bg'>
        <strong>  <br />  </strong>
        <p>  30,000</p>
      </div>
      {
        alert === true
          ? <div className='alert alert-danger'>5   20% </div>
          : null
      }
      <div className="contents">
        <p className="title">  <br />  </p>

        <div className="event_cont event_cont01">
          <p> , <br />  !<span> 30,000/<em></em></span></p>
        </div>

        <div className="event_cont event_cont02">
          <p><br /> <span> !</span></p>
        </div>

        <div className='event_bottom_text'>
          <p className="title"> <br />   </p>
          <p className="desc">   ! <br/>    :)</p>
          <button> </button>
        </div>
      </div>
    </div>
  )
}

export default Sub3