import React from 'react';
import { useNavigate } from 'react-router-dom';
import AuthorCard from '../component/AuthorCard';


const ExperienceInfo = ({ pic, showButton, btnDataClick }) => {
  
  const navigate = useNavigate();

  const goToSub2 = (id) => {
    navigate(`/sub2-1/${id}`)
  }

  return (
    <>
      <div className='container authorList'>
        <h2 className='mb30'>HOT ITEM </h2>
        <p className="desc mb30">INNISFREE  5000~    !</p>

        <div className='authcard_wrap'>
          {
            pic.map((item, i) => (
              <AuthorCard key={i} item={item} id={i} onClick={goToSub2} index={i} />
            ))
          }
        </div>
      </div>

      {/*   */}
      {
        //     
        showButton && (
          <button className="btn-data" onClick={btnDataClick} disabled={!showButton}>  </button>
        )
      }
    </>


  )
}

export default ExperienceInfo